body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.transition-color {
  -webkit-transition: color 1s;
  -o-transition: color 1s;
  transition: color 1s;
  will-change: color;
}

.transition-opacity {
  -webkit-transition: opacity 1s;
  -o-transition: opacity 1s;
  transition: opacity 1s;
  will-change: opacity;
}

.opacity-0 {
  opacity: 0
}

.opacity-1 {
  opacity: 1
}

.transition-background-color {
  -webkit-transition: background-color 1s;
  -o-transition: background-color 1s;
  transition: background-color 1s;
  will-change: background-color;
}

.app {
  height: 100vh;
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.header {
  background-color: #222;
  -ms-flex: 0 0 80px;
      flex: 0 0 80px;
  padding: 0px;
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.header__title {
  margin: 0;
  padding: 0;
}

.main {
  -ms-flex: 1 0;
      flex: 1 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}



